	module.exports = {"data":{"markdownRemark":{"html":"<h2>Ruby Arrays</h2>\n<p>An array represents a list of values. The individual values are often called “elements” of the array. To make an array in Ruby, use square brackets and separate values with commas:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-ruby\"><code>my_array <span class=\"token operator\">=</span> <span class=\"token punctuation\">[</span><span class=\"token number\">1</span><span class=\"token punctuation\">,</span> <span class=\"token number\">2</span><span class=\"token punctuation\">,</span> <span class=\"token number\">3</span><span class=\"token punctuation\">,</span> <span class=\"token number\">4</span><span class=\"token punctuation\">,</span> <span class=\"token number\">5</span><span class=\"token punctuation\">]</span>\n</code></pre>\n      </div>\n<p>That first example is an array of numbers, but a Ruby array can contain values of different types, even other arrays:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-ruby\"><code>mixed_array <span class=\"token operator\">=</span> <span class=\"token punctuation\">[</span><span class=\"token number\">5</span><span class=\"token punctuation\">,</span> <span class=\"token string\">\"Hello World\"</span><span class=\"token punctuation\">,</span> <span class=\"token keyword\">true</span><span class=\"token punctuation\">,</span> <span class=\"token punctuation\">[</span><span class=\"token number\">1</span><span class=\"token punctuation\">,</span><span class=\"token number\">2</span><span class=\"token punctuation\">,</span><span class=\"token number\">3</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">]</span>\n</code></pre>\n      </div>\n<p>You can access the elements of an array with square brackets and numerical indexes. Notice that the first element is at index 0, not 1:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-ruby\"><code>mixed_array<span class=\"token punctuation\">[</span><span class=\"token number\">0</span><span class=\"token punctuation\">]</span> <span class=\"token comment\" spellcheck=\"true\"># 5</span>\nmixed_array<span class=\"token punctuation\">[</span><span class=\"token number\">1</span><span class=\"token punctuation\">]</span> <span class=\"token comment\" spellcheck=\"true\"># \"Hello World\"</span>\nmixed_array<span class=\"token punctuation\">[</span><span class=\"token number\">2</span><span class=\"token punctuation\">]</span> <span class=\"token comment\" spellcheck=\"true\"># true</span>\n</code></pre>\n      </div>\n<p>You can check how many elements an array has with the <code>length</code> method:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-ruby\"><code>mixed_array<span class=\"token punctuation\">.</span>length <span class=\"token comment\" spellcheck=\"true\"># 3</span>\n<span class=\"token punctuation\">[</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">.</span>length <span class=\"token comment\" spellcheck=\"true\"># 0</span>\n</code></pre>\n      </div>\n<h4>Ruby Lambda</h4>\n<p>A lambda is also commonly referred to as an anonymous function. To create a lambda in Ruby, you can use the following syntax:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-ruby\"><code>lambda <span class=\"token operator\">=</span> lambda <span class=\"token punctuation\">{</span><span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h4>More Information:</h4>\n<p><a href='https://ruby-doc.org/core-2.4.2/Array.html' target='_blank' rel='nofollow'>Ruby array documentation</a></p>","fields":{"slug":"/ruby/ruby-arrays/"},"frontmatter":{"title":"Ruby Arrays"}}},"pathContext":{"slug":"/ruby/ruby-arrays/"}}

